<style lang="scss">
  .stickies {
    font-size: .16rem;
    .ana {
      margin: 0 auto;
      width: 90%;
      padding-top: 1em;
      > .content {
        display: block;
        text-align: left;
        font-family: cursive, serif;
        font-weight: 600;
        font-size: .18rem;
        &::before {
          content: '';
        }
        &::after {
          content: '';
        }
      }
      > .author {
        display: block;
        text-align: right;
        font-size: .12rem;
        &::before {
          content: ' ';
        }
      }
    }
    .body {
      padding: 1em 5%;
    }
    .item {
      float: left;
      width: 25%;
      padding: .07rem;
    }
    .card {
      box-shadow: 1px 1px 6px rgba(0,0,0,.1);
      .img {
        padding-top: 30.9%;
        background: center / cover;
      }
      p.title {
        padding: 0 1em;
        text-align: left;
        line-height: 2;
        font-weight: 600;
        font-size: .18rem;
        font-family: cursive, serif;
      }
      p.intro {
        font-size: .13rem;
        padding: 0 1em;
        text-align: left;
        line-height: 1.5;
        font-family: cursive, serif;
      }
    }
  }
  @media only screen and (max-width: 500px) {}
</style>

<template>
  <main class="stickies">
    <div class="ana">
      <span class="content"></span>
      <span class="author"> </span>
    </div>
    <div class="body clearfix">
      <div class="item" v-for="i in 9">
        <div class="card">
          <div class="img" :style="{backgroundImage: `url(http://pic.liebedich.cn/00${i}.jpg)`}"></div>
          <p class="title"></p>
          <p class="intro"></p>
          <div><span>: 666</span><span>: 0</span></div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
  export default {};
</script>


